/*contact styling*/

#formSheet {
  text-align: center;
}

#Submit {
  right: 6%;
  position: relative;

}

#contactMe {
  text-align: center;
  font-family: 'Monda';
  color: white;
  padding: 5px;
  letter-spacing: 2px;


}

.intro {
  /* align-items: center; */
  padding-left: 19vw;
}
#myHeader {
  text-align: center;
  font-weight: 900;
  font-family: 'Monda';
  color: white;
  padding: 5px;
  font-size: 29px;
}



#goku {
  border-radius: 25%;
  width: 10vw;
  height: 10vw;
  border: 1px solid #C7AA5F;
  margin-left: 11.2vw;
  margin-right: 5px;
  /* margin-top: 3px;
  padding-bottom: 5px; */

}

#gokuM {
  border-radius: 50%;
  display: block;
  width: 30vw;
  height: 30vw;
  border: 1px solid #C7AA5F;
  /* display: inline-block; */
  /* margin-left: 1.6vw;
  margin-right: 5px;
  margin-top: 1.6vw; */
  /* margin-top: 3px;
  padding-bottom: 5px; */

}

.email {
  padding-left: 3vw;
  display: -ms-inline-flexbox;
  display: inline-flex;
}
.email img {
  width:2.3vw;
  height: 2.3vw;



}
.email p:nth-child(1){
  font-size: 1.3vw;
  margin-left: 12vw;
}

.email p:nth-child(2){
  font-size: 1.3vw;
  padding-left: 0.4vw;
  padding-top: 0.4vw;

}

.containerThing{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}


.test {
  margin: 7px;
}
#paraM {
  margin-top: 15px;
  width: 50vw;
}

#goku:hover{
  /* filter: grayscale(0%);
  filter:invert(0.1); */
  -webkit-box-shadow:
        0 0 1px 3px #F9D477,  /* inner white */
        0 0 30px 10px #F9D477, /* middle magenta */
        0 0 10px 7px #C7AA5F;
          box-shadow:
        0 0 1px 3px #F9D477,  /* inner white */
        0 0 30px 10px #F9D477, /* middle magenta */
        0 0 10px 7px #C7AA5F; /* outer cyan */
}

.test {
  display: -ms-flexbox;
  display: flex;
  padding-bottom: 15px;
}



